import React from "react";
import { Jumbotron } from "reactstrap";

const jumbtronInstrance = (props) => {
	return (
		<div>
			<Jumbotron>
				<h1 className="display-3">NYTimes Search</h1>
			</Jumbotron>
		</div>
	);

};

export default jumbtronInstrance;


